import './displayPage.css'
import playButton from './static/Play-Button.svg'
import { useEffect } from 'react';

import Embed from 'flat-embed'

//Flat.io
const renderScore = () => {
    const container = document.getElementById('score')
    const appId = '63bf79cf24013e27391d7a98'
    const embed = new Embed('score', {
        embedParams: {
          appId,
          layout: 'track',
          controlsDisplay: false,
        }
      });
      fetch('./beet.xml')
        .then(response => response.text())
        .then(xml => embed.loadMusicXML(xml))
        .then(() => embed.setTrack({
          id: 'external-1',
          type: 'external',
          totalTime: 276,
          synchronizationPoints: [
            { type: 'measure', time: 0, location: { measureIdx: 0 }},
            { type: 'end', time: 19 }
          ],
        }))
        .then(() => embed.useTrack({ id: 'external-1' }))
        .then(() => console.log('Embed loaded'))
        .catch(error => console.error(error));
}


const DisplayPage = () => {
    useEffect(() => {
        document.documentElement.className = "DisplayPageBody";
        document.body.className = "DisplayPageBody";
        document.getElementById("root").className = "DisplayPageBody";
        const app = document.getElementsByClassName("App")
        app[0].setAttribute('id', 'DisplayPageBody')
        renderScore()

    }, []);

    return (
        <main id="displayPage">
            <div className="music-type">
                <h1>Music Type</h1>
            </div>

            <div className="display-window">
                <div className="score-title">
                    Score Name
                </div>
                <div className="score" id='score'>

                </div>
                <div className="play-bar">
                    <button></button>
                </div>
            </div>
        </main>
    )
}

  

export {DisplayPage};